"use client";

import { useEffect, useState, useCallback } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import Image from "next/image";
import { supabase } from "@/lib/supabaseClient";
import { AuthCard } from "./components/AuthCard";
import { Header } from "@/components/ui/Header";
import { CosmoLoader } from "@/components/ui/CosmoLoader";
import type { PhotoAvatarRow } from "@/app/avatars/servers/UploadAvatar";

export const dynamic = 'force-dynamic';

interface VoiceRow {
  id?: string;
  uid: string;
  name: string;
  url: string;
  created_at?: string;
  description?: string | null;
}

export default function Home() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [avatars, setAvatars] = useState<PhotoAvatarRow[]>([]);
  const [voices, setVoices] = useState<VoiceRow[]>([]);
  const [isLoadingData, setIsLoadingData] = useState(false);

  useEffect(() => {
    const checkAuth = async () => {
      const {
        data: { user: currentUser },
      } = await supabase.auth.getUser();
      setUser(currentUser);
      setIsLoading(false);
      
      if (currentUser) {
        fetchDashboardData();
      }
    };

    checkAuth();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchDashboardData = useCallback(async () => {
    setIsLoadingData(true);
    
    const {
      data: { user: currentUser },
    } = await supabase.auth.getUser();

    if (!currentUser) {
      setIsLoadingData(false);
      return;
    }

    // Загружаем аватары (первые 6)
    const { data: avatarsData } = await supabase
      .from("photo_avatars")
      .select("*")
      .eq("uid", currentUser.id)
      .order("created_at", { ascending: false })
      .limit(6);

    // Загружаем голоса (первые 6)
    const { data: voicesData } = await supabase
      .from("voices")
      .select("*")
      .eq("uid", currentUser.id)
      .order("created_at", { ascending: false })
      .limit(6);

    setAvatars(avatarsData ?? []);
    setVoices((voicesData ?? []).filter((v) => v.url && v.name));
    setIsLoadingData(false);
  }, []);

  if (isLoading) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-white">
        <CosmoLoader ariaLabel="Загрузка..." />
      </div>
    );
  }

  if (!user) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-white px-4 py-8">
        <AuthCard />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      <Header />
      <main className="mx-auto w-full max-w-[1350px] px-6 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Главная</h1>
          <p className="mt-2 text-gray-600">Обзор ваших аватаров, голосов и материалов</p>
        </div>

        {isLoadingData ? (
          <div className="flex justify-center py-12">
            <CosmoLoader ariaLabel="Загружаем данные..." />
          </div>
        ) : (
          <div className="space-y-12">
            {/* Секция аватаров */}
            <section>
              <div className="mb-6 flex items-center justify-between">
                <h2 className="text-2xl font-bold text-gray-900">Мои аватары</h2>
                <Link
                  href="/avatars"
                  className="text-blue-600 hover:text-blue-700 font-semibold transition"
                >
                  Все аватары →
                </Link>
              </div>
              {avatars.length === 0 ? (
                <div className="rounded-2xl border border-gray-200 bg-gray-50 p-12 text-center">
                  <p className="text-gray-600">У вас пока нет аватаров</p>
                  <Link
                    href="/avatars"
                    className="mt-4 inline-block rounded-lg bg-blue-600 px-4 py-2 text-sm font-semibold text-white transition hover:bg-blue-500"
                  >
                    Создать аватар
                  </Link>
                </div>
              ) : (
                <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6">
                  {avatars.map((avatar) => {
                    const key =
                      avatar.id ??
                      avatar.hey_gen_id ??
                      avatar.group_id ??
                      `${avatar.uid}-${avatar.image_key}`;
                    return (
                      <Link
                        key={key}
                        href="/avatars"
                        className="group relative rounded-3xl border border-gray-100 bg-white p-4 shadow-sm ring-1 ring-black/5 transition-all duration-300 hover:-translate-y-2 hover:shadow-lg"
                      >
                        <p className="mb-3 text-center text-lg font-semibold text-gray-900 line-clamp-2">
                          {avatar.name}
                        </p>
                        <div className="relative aspect-[3/4] w-full overflow-hidden rounded-2xl">
                          <Image
                            src={avatar.photo}
                            alt={`Фото аватара ${avatar.name}`}
                            fill
                            sizes="(min-width: 1280px) 16vw, (min-width: 1024px) 33vw, (min-width: 640px) 50vw, 100vw"
                            className="object-contain"
                            unoptimized
                          />
                        </div>
                      </Link>
                    );
                  })}
                </div>
              )}
            </section>

            {/* Секция голосов */}
            <section>
              <div className="mb-6 flex items-center justify-between">
                <h2 className="text-2xl font-bold text-gray-900">Мои голоса</h2>
                <Link
                  href="/voices"
                  className="text-purple-600 hover:text-purple-700 font-semibold transition"
                >
                  Все голоса →
                </Link>
              </div>
              {voices.length === 0 ? (
                <div className="rounded-2xl border border-gray-200 bg-gray-50 p-12 text-center">
                  <p className="text-gray-600">У вас пока нет голосов</p>
                  <Link
                    href="/voices"
                    className="mt-4 inline-block rounded-lg bg-purple-600 px-4 py-2 text-sm font-semibold text-white transition hover:bg-purple-500"
                  >
                    Создать голос
                  </Link>
                </div>
              ) : (
                <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6">
                  {voices.map((voice) => {
                    const key = voice.id ?? `${voice.uid}-${voice.name}`;
                    return (
                      <Link
                        key={key}
                        href="/voices"
                        className="group relative rounded-3xl border border-gray-100 bg-white p-6 shadow-sm ring-1 ring-black/5 transition-all duration-300 hover:-translate-y-2 hover:shadow-lg"
                      >
                        <div className="flex items-center gap-4">
                          <div className="flex h-12 w-12 shrink-0 items-center justify-center rounded-full bg-purple-600 text-white">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              viewBox="0 0 24 24"
                              fill="currentColor"
                              className="h-6 w-6"
                            >
                              <path d="M8 5v14l11-7z" />
                            </svg>
                          </div>
                          <div className="min-w-0 flex-1">
                            <p className="text-lg font-semibold text-gray-900 line-clamp-2">
                              {voice.name}
                            </p>
                          </div>
                        </div>
                        {voice.description && (
                          <p className="mt-4 line-clamp-2 text-sm text-gray-600">
                            {voice.description}
                          </p>
                        )}
                      </Link>
                    );
                  })}
                </div>
              )}
            </section>

            {/* Секция материалов/видео */}
            <section>
              <div className="mb-6 flex items-center justify-between">
                <h2 className="text-2xl font-bold text-gray-900">Мои материалы</h2>
                <Link
                  href="/material"
                  className="text-green-600 hover:text-green-700 font-semibold transition"
                >
                  Все материалы →
                </Link>
              </div>
              <div className="rounded-2xl border border-gray-200 bg-gray-50 p-12 text-center">
                <p className="text-gray-600">У вас пока нет материалов</p>
                <Link
                  href="/material/editor"
                  className="mt-4 inline-block rounded-lg bg-green-600 px-4 py-2 text-sm font-semibold text-white transition hover:bg-green-500"
                >
                  Создать материал
                </Link>
              </div>
            </section>
          </div>
        )}
      </main>
    </div>
  );
}
